


















































































import Vue from 'vue';
import PopUp from '@/configurator/components/utils/PopUp.vue';
import {Nullable} from '@/common/utils/types';
// @ts-ignore
import SvgCheck from '@/common/svgs/check.svg';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import RadioButton from '@/configurator/components/utils/RadioButton.vue';
import GdprEmailInput from '@/configurator/components/utils/GdprEmailInput.vue';
import PrimaryButton from '@/configurator/components/utils/PrimaryButton.vue';

export default Vue.extend({
  components: {
    PopUp,
    SvgIcon,
    RadioButton,
    GdprEmailInput,
    PrimaryButton,
  },

  data() {
    return {
      perspectiveImage: null as Nullable<string>,
      configurationId: null as Nullable<string>,
      email: '',
      gdprValid: false,
      fileFormat: null as Nullable<string>,
      hasError: false as boolean,
    };
  },

  methods: {
    async send() {
      this.hasError = false;
      try {
        const currentConfiguration = await this.$sdkConnector.saveCurrentConfiguration();
        const rapiAccess = await this.$sdkConnector.getRapiAccess();
        const options = (this.fileFormat === 'glTF') ? {binary: true} : {};
        await rapiAccess.export3D(currentConfiguration.id, this.email, this.fileFormat!!, options);
        this.$props.onClose();
      } catch (e) {
        console.error(e);
        this.hasError = true;
      }
    },
    onEmailChange(isValid: boolean, email: string, _gdprChecked: boolean) {
      this.hasError = false;
      this.gdprValid = isValid;
      this.email = email;
    },
    onFileFormatChange() {
      this.hasError = false;
      this.fileFormat = (this.$el.querySelector('input[name="fileFormat"]:checked') as HTMLInputElement)?.value || null;
    },
  },

  computed: {
    icons(): object {
      return {SvgCheck};
    },
    buttonEnabled(): boolean {
      return this.gdprValid && !!this.fileFormat;
    },
  },

  async mounted() {
    await this.$sdkConnector.waitForLoad();
    const configurator = await this.$sdkConnector.configuratorApi;
    this.configurationId = await configurator.getCurrentConfigurationHash();
    const {image} = await configurator.preparePerspectiveImage();
    this.perspectiveImage = image;
    this.hasError = false;
  },

  destroyed() {
    this.perspectiveImage = null;
    this.hasError = false;
  },

  props: {
    onClose: Function,
  },

});
